export const initialState = {
  basket: [],
  user: null,
};
export const getBasketTotal=(basket)=>{
  const sum=basket?.reduce((amount,item)=>item.price + amount,0);
  return sum;
}
const reducer = (state, action) => {
  console.log(action);
  switch (action.type) {
    case "ADD_TO_BASKET":
      //logic for adding items gto  basket
      return { ...state, basket: [...state.basket, action.item] };
    case "REMOVE_FROM_BASKET":
      //logic for removing items from  basket

      //cloned the basket
      let newBasket = [...state.basket];
      const index = state.basket.findIndex(
        (basketItem) => basketItem.id === action.id
      );
      if (index >= 0) {
        //item exist in basket and remove it
        newBasket.splice(index, 1);
      } else {
        console.warn(
          "Cant remove product (id:${action.id}) as its not existing"
        );
      }
      return { ...state, basket: newBasket };
    default:
      return state;
  }
};
export default reducer;
